
.wrapper {
    z-index: 1;
    position: absolute;
    height: 100%;
    width: 100%;
    perspective: 600px;
}

.miniPlayer {
    width: fit-content;
    height: fit-content;
    perspective: inherit;
    left: 50%;
    top: calc(50% - 40px);
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    transition: 1s top linear, .1s transform linear;
    position: absolute;
    font-family: 'Garamond';
    font-size: 20px;
    font-weight: 200;
    font-style: italic;
    color: white;
    /**animation: moveArroud 1s infinite alternate;**/
}

.albumArt {
    width: 300px;
    height: 300px;
    border-radius: 15px;
    margin-bottom: 10px;
    position: relative;
    transition: .1s bottom linear, .1s transform ease-in;
    bottom: 0;
    /** animation: shakeIt 1s infinite alternate; **/
}

.albumArt:hover {
    cursor: pointer;
    transform: rotateX(15deg);
}

@keyframes moveArroud {
    from {
        transform: translate(-50%, -50%) rotateX(0deg) ;
    }
    to {
        transform: translate(-50%, -50%) rotateX(10deg) ;
    }
}

@keyframes slideIn {
    0% {
        top: calc(50% - 40px);
        pointer-events: none;
    }
    50% {
        top: calc(50% - 320px);
        pointer-events: none;
    }
    100% {
        top: calc(50% - 40px);
        pointer-events: none;
    }
}

@keyframes shakeIt {
    0% {
        transform: translate(-50%, -50%) rotateX(-5deg) rotateZ(1deg) translateY(10px);
    }
    100% {
        transform: translate(-50%, -50%) rotateX(5deg) rotateZ(-1deg) translateY(10px);
    }
}

.showing {
    animation: slideIn 1s forwards;
}

.showingBack {
    animation: slideIn 1s backwards;
}

.info {
    opacity: 0;
    transition: .5s opacity ease-in;
}

.show {
 opacity: 1;
}
